
@font-face {
  font-family: 'Gilroy';
  src: url('/static/fonts/Gilroy-Regular.eot');
  src: url('/static/fonts/Gilroy-Regular.eot?#iefix') format('embedded-opentype'),
    url('/static/fonts/Gilroy-Regular.woff2') format('woff2'),
    url('/static/fonts/Gilroy-Regular.woff') format('woff'),
    url('/static/fonts/Gilroy-Regular.ttf') format('truetype'),
    url('/static/fonts/Gilroy-Regular.svg#Gilroy-Regular') format('svg');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Gilroy';
  src: url('/static/fonts/Gilroy-Medium.eot');
  src: url('/static/fonts/Gilroy-Medium.eot?#iefix') format('embedded-opentype'),
    url('/static/fonts/Gilroy-Medium.woff2') format('woff2'),
    url('/static/fonts/Gilroy-Medium.woff') format('woff'),
    url('/static/fonts/Gilroy-Medium.ttf') format('truetype'),
    url('/static/fonts/Gilroy-Medium.svg#Gilroy-Medium') format('svg');
  font-weight: 500;
  font-style: normal;
}

/*
  BASE
  ========
  html is set to 62.5% so that all the REM measurements are based on 10px sizing.
  So basically 1.5rem = 15px :)
*/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}
html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}
body {
  color: #68788c;
  font-family: 'Gilroy', 'Helvetica', sans-serif;
  font-size: 1.5rem;
  line-height: 1.6;
  background: -webkit-linear-gradient(bottom, #fff, #f6f8fd 80%) fixed;
  background: linear-gradient(0deg, #fff, #f6f8fd 80%) fixed;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  overflow-x: hidden;
}
*, *:before, *:after {
  box-sizing: inherit;
}
h1, h2, h3, h4, h5, h6 {
  font-weight: 500;
  margin-top: 0;
  margin-bottom: 2.5rem;
  white-space: nowrap;
}
h1 { line-height: 1.2; font-size: 4.0rem;
}
h2 { line-height: 1.25; font-size: 2.6rem;
}
h3 { line-height: 1.3; font-size: 2.0rem;
}
h4 { line-height: 1.35; font-size: 1.6rem;
}
h5 { line-height: 1.5; font-size: 1.4rem;
}
h6 { line-height: 1.6; font-size: 1.2rem;
}
p {
  margin-top: 0;
}
figure {
  margin: 0;
}
img {
  max-width: 100%;
}

/*
  PAGE
  ========
*/
.scene {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 30px 65px;
}
.content-wrapper {
  position: relative;
  background: #fff;
  max-width: 0;
  overflow: hidden;
}
.content {
  padding: 120px 60px 70px 100px;
}
.content-title {
  display: inline-block;
  padding-right: 20px;
}
.content-section {
  margin-bottom: 3rem;
  line-height: 2;
}
.content-header > h3 {
  display: inline-block;
  margin-bottom: 1rem;
}
.content-line {
  position: relative;
  display: inline-block;
  white-space: nowrap;
}
.faded__content {
  color: #cdd2d6;
}
.reveal__content {
  position: absolute;
  overflow: hidden;
  left: 0;
  width: 0;
}
.block-revealer__element {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000;
  pointer-events: none;
  opacity: 0;
}
.footer {
  pointer-events: none;
  position: absolute;
  bottom: 0;
  left: -50px;
  right: -50px;
}
@media (max-width: 750px) {
.content-line {
    display: inline;
    white-space: normal;
}
}
@media (max-width: 500px) {
.content-wrapper {
    max-width: 100%;
}
.scene {
    padding: 0 0 90px;
}
.content {
    padding: 120px 40px 30px;
}
.fg {
    left: -20px;
    right: -20px;
}
}



